import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { loginUser, disableHelpMessage } from '../../store/userSlice/userSlice';
import './Login.css';

function Login() {
  const dispatch = useDispatch();
  const isUser = useSelector((state) => state.user.isUser);
  const helpMessage = useSelector((state) => state.user.helpMessage);
  const navigate = useNavigate();

  //  helpMessage   
  useEffect(() => () => {
    dispatch(disableHelpMessage());
  }, [dispatch]);

  function loginSubmit(event) {
    event.preventDefault();
    const data = {
      email: event.target.email.value,
      password: event.target.password.value,
    };
    dispatch(loginUser(data));
  }

  useEffect(() => {
    if (isUser) {
      navigate('/');
    }
  }, [isUser, navigate]);

  return (
    <>
      <div className="App">
        <div className="login_form_conteiner">
          <form className="login_form" onSubmit={loginSubmit}>
            <h4></h4>
            <label htmlFor="emailInput" >Email</label>
              <input
                className='font_caption'
                type="email"
                name="email"
                id='emailInput'
                placeholder="Email"
                pattern="^\S+@\S+\.\S+$"
                title='      email@mail.com'
                required
              />
            <label htmlFor="passwordInput"></label>
              <input
                className='font_caption'
                type="password"
                name="password"
                id='passwordInput'
                placeholder=""
                // pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
                title="     8 ,       ,     "
                required
              />
            { helpMessage && <div className="helpText">{helpMessage}</div>}
            <button className='login_btn' type="submit">
              
            </button>
          </form>
        </div>
      </div>
    </>
  );
}

export default Login;
