import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { regUser, disableHelpMessage } from '../../store/userSlice/userSlice';
import './Registration.css';

function Registration() {
  const dispatch = useDispatch();
  const isUser = useSelector((state) => state.user.isUser);
  const helpMessage = useSelector((state) => state.user.helpMessage);
  const navigate = useNavigate();

  //  helpMessage   
  useEffect(() => () => {
    dispatch(disableHelpMessage());
  }, [dispatch]);

  function regSubmit(event) {
    event.preventDefault();
    const data = {
      displayName: event.target.displayName.value,
      email: event.target.email.value,
      login: event.target.login.value,
      city: event.target.city.value,
      password: event.target.password.value,
      repeatPass: event.target.repeatPass.value,
    };
    dispatch(regUser(data));
  }

  useEffect(() => {
    if (isUser) {
      navigate('/');
    }
  }, [isUser, navigate]);

  return (
    <>
      <div>
        <form className="registration_form" onSubmit={regSubmit}>
          <h4></h4>

          <label htmlFor="nameInput"></label>
          <input type="text" name="displayName" id='nameInput' placeholder="" required />

          <label htmlFor="emailInput" >Email</label>
          <input
            type="email"
            name="email"
            id='emailInput'
            placeholder="Email"
            pattern="^\S+@\S+\.\S+$"
            title='      email@mail.com'
            required
          />

          <label htmlFor="loginInput">Login</label>
          <input type="text" name="login" id='loginInput' placeholder="Login" required />

          <label htmlFor="cityInput"></label>
          <select className='select-css' id='cityInput' name="city">
            <option>-</option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option> </option>
          </select>

          <label htmlFor="passwordInput"></label>
          <input
            type="password"
            name="password"
            id='passwordInput'
            placeholder=""
            pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
            title="     8 ,       ,     "
            required
          />

          <label htmlFor="repeatPassInput"> </label>
          <input
            type="password"
            name="repeatPass"
            id='repeatPassInput'
            placeholder=" "
            required
          />
          { helpMessage && <div className="helpText">{helpMessage}</div>}
          <button className='registration_btn' type="submit"></button>
        </form>
      </div>
    </>
  );
}

export default Registration;
