import React from 'react';
// import userSlice from '../../store/userSlice/userSlice';
import { useSelector } from 'react-redux';
import { NavLink, useNavigate } from 'react-router-dom';
import './UserPage.css';
import CardPlace from '../CardPlace/CardPlace.jsx';
import avatar from './images/avatar.png';

function UserPage() {
  const user = useSelector((state) => state.user.data);
  const navigate = useNavigate();

  return (
    <div className='profile_container'>
      <div className="profile_content">
        <div className='backbtn'><NavLink onClick={() => navigate('/')} to='/'> </NavLink></div>
        <div className='profile'>
          <div className='user_info'>
            {user.photo && <img src={user.photo} alt="" className="user_photo"/>}
            {!user.photo && <img src={avatar} alt="" className="user_photo"/>}
            <div className='user_name'>{user.displayName}</div>
          </div>
          <div className='user_data'>
            <div className='my_data'> </div>
            <div className='details'>: {user.login}</div>
            <div className='details'>: {user.email}</div>
            <div className='details'>: {user.city}</div>
            {
              user.sex
                ? <div className='details'>: {user.sex}</div>
                : <div className='details'>:  </div>
            }
            {
              user.age
                ? <div className='details'>: {user.age}</div>
                : <div className='details'>:  </div>
            }
            {
              user.about
                ? <div className='details'> : {user.about}</div>
                : <div className='details'> :  </div>
            }
            <NavLink className='edit_btn' to="/settings"></NavLink>
          </div>
        </div>
        <div className='places'>
          <div className='my_places'>
            <h3> </h3>
            <NavLink to="/newplace"> </NavLink>
            <div className='cards'>
              {user.places
              && user.places.length
              && user.places.map((place) => <CardPlace key={place.id} place={place}/>)}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default UserPage;
