/* eslint-disable no-unused-vars */
/* eslint-disable max-len */
import { useEffect } from 'react';
import './UserSettingsPage.css';
import { NavLink, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { changeUser, disableHelpMessage } from '../../store/userSlice/userSlice';
import avatar from './images/avatar.png';

function UserSettingsPage() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const user = useSelector((state) => state.user.data);
  const helpMessage = useSelector((state) => state.user.helpMessage);

  useEffect(() => () => {
    dispatch(disableHelpMessage());
  }, [dispatch]);

  const settingsSubmit = async (event) => {
    event.preventDefault();
    const data = new FormData(event.target);
    dispatch(changeUser({ data, userId: user.id }));
    navigate('/profile');
  };

  return (
    <>
      <div className="profile_container">
        <div className="profile_content">
          <div className="backbtn">
            <NavLink to="/profile">
              
            </NavLink>
          </div>
          <div className="profile">
            <div className="user_info">
              {user.photo && (
                <img src={user.photo} alt="" className="user_photo" />
              )}
              {!user.photo && (
                <img src={avatar} alt="" className="user_photo" />
              )}
            </div>
            <div className="user_data">
              <form onSubmit={settingsSubmit} className="user_settings_form">
                <h4> </h4>

                <input
                  name="photo"
                  className="photoInput"
                  type="file"
                  accept="image/*,.png,.jpg,.jpeg"
                />

                <label htmlFor="nameInput"></label>
                <input
                  type="text"
                  name="displayName"
                  id="nameInput"
                  placeholder=""
                  defaultValue={user.displayName}
                  required
                />

                <label htmlFor="emailInput">Email</label>
                <input
                  type="email"
                  name="email"
                  id="emailInput"
                  placeholder="Email"
                  pattern="^\S+@\S+\.\S+$"
                  title="      email@mail.com"
                  defaultValue={user.email}
                  required
                />

                <label htmlFor="loginInput">Login</label>
                <input
                  type="text"
                  name="login"
                  id="loginInput"
                  placeholder="Login"
                  defaultValue={user.login}
                  required
                />

                <label htmlFor="cityInput"></label>
                <select name="city" id="cityInput" defaultValue={user.city}>
                  <option>-</option>
                  <option></option>
                  <option></option>
                  <option></option>
                  <option></option>
                  <option></option>
                  <option> </option>
                </select>

                <label htmlFor="sexInput"></label>
                <select name="sex" id="sexInput" defaultValue={user.sex}>
                  <option></option>
                  <option></option>
                  <option></option>
                </select>

                <label htmlFor="aboutInput"></label>
                <input
                  type="number"
                  name="age"
                  id="aboutInput"
                  placeholder=""
                  defaultValue={user.age}
                />

                <label htmlFor="aboutInput">  </label>
                <textarea
                  type="text"
                  name="about"
                  id="aboutInput"
                  className="about_input"
                  placeholder=" "
                  defaultValue={user.about}
                />

                <label htmlFor="passwordInput"></label>
                <input
                  type="password"
                  name="password"
                  id="passwordInput"
                  placeholder=" "
                  pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
                  title="     8 ,       ,     "
                />

                <label htmlFor="repeatPassInput"> </label>
                <input
                  type="password"
                  name="repeatPass"
                  id="repeatPassInput"
                  placeholder=" "
                />
                {helpMessage && <div className="helpText">{helpMessage}</div>}

                <button className="user_settings_btn" type="submit">
                  
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default UserSettingsPage;
